.widgets{
     flex:0.2;
     position: sticky;
     top:80px;
     background-color: white;
     border-radius: 10px;
     border:1px solid lightgray;
     height: fit-content;
     padding-bottom: 10px;  
     margin-right: 25rem;
     

}
.widgets_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.widgets_header > h2 {
    font-size: 16px;
    font-weight: 400;
}

.widgets_articles{
    display: flex;
    padding: 10px;
    cursor:pointer;
}
.widgets_articles:hover{
    background-color: whitesmoke;
}
.widgets_articleLeft{
    color: #0177b7;
    margin-right: 5px;

}

.widgets_articleLeft > .MuiSvgIcon-root{
    font-size:15px
}
.widgets_articleRight{
flex:1;
}

.widgets_articleRight > h4{
font-size: 20px;
}
.widgets_articleRight > p{
font-size: 12px;
color: gray;
}




