.sidebar {
  position: sticky;
  top: 80px;
  flex: 0.2;
  border-radius: 10px;
  text-align: center;
  height: fit-content;
  margin-left: 25rem;
  
}

.sidebar__avatar {
  margin-bottom: 10px;
  height: 80px !important;
  width: 80px !important;
}
.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar__top h4 {
  color: gray;
  font-size: 12px;
}
.sidebar__top h2 {
  font-size:18px;
}
.sidebar__top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}
.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar__stat > p {
  color: gray;
  font-size: 12px;
  font-weight: 600;
}
.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sidebar__statNumber {
  font-weight: bold !important;
  color: #0a66c2 !important;
}
.sidebar__bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}
.sidebar__bottom > p {
  margin-bottom: 10px;
}

/* for the resuablefuntion */
.sidebar__recentItems {
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}

.sidebar__recentItems:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}

.sidebar__hash {
  margin: 0 10px;
}

.sidebar__bottom2 {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}

.sidebar__bottom2 > p {
  margin-bottom: 10px;
}

.sidebar__recentItems2 {
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}

.sidebar__recentItems2:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}

.sidebar__icon {
  color: #0177b7;
}
.sidebar__recentItems2 > p {
  color: rgb(75, 72, 72);
  margin-top: 3px;
  margin-left: 5px;
}
.sidebar__bottom2 .more  {
  color:rgb(0, 0, 0);
  text-decoration: underline;
  display: flex;
  justify-content: space-evenly;
  cursor: pointer;
 
}
